*{
  margin: 0;
  box-sizing: border-box;
}
body{
  margin-top: 50px;
  background-color: blueviolet;
  letter-spacing: 1px;
}
h1{
  font-size: 35px;
  padding-bottom: 10px;
  text-align: center;
}
.container{
  width: 40%;
  background-color: antiquewhite;
  margin: auto;
  padding-left: 10px;
}
img{
  border-radius: 50%;
  display: flex;
  margin: 10px;
}
.btn{
  width: 80%;
  margin: 10px 20px 10px 30px;
  padding-top: 5px;
  padding-bottom: 5px;
  font-size: 15px;
  background-color: blueviolet;
  border-style: none;
  border-radius: 5px;
  color: whitesmoke;
  letter-spacing: 1px;
}
.item{
  display:flex;

}
h2{
  margin: 15px;
}
p{
  margin: auto;

}